





































































import axios, { AxiosError } from 'axios';
import { Vue, Component, Emit } from 'vue-property-decorator';
import {
  mdiAccount,
  mdiEye,
  mdiEyeOff,
  mdiLock,
  mdiClose,
} from '@mdi/js';
import Logo from '@/components/icons/Logo.vue';

@Component({
  components: {
    Logo,
  },
})
export default class LoginForm extends Vue {
  private get darkMode(): boolean {
    return this.$vuetify.theme.dark;
  }

  private set darkMode(value: boolean) {
    this.$vuetify.theme.dark = Boolean(value);
    localStorage.darkmode = Boolean(value);
  }

  private valid = true

  private isLoading = false

  private showPassword = false

  private rules: Function[] = [
    (v: string) => !!v || 'Это поле обязательно для заполнения',
  ]

  private icons: { [key: string]: string } = {
    mdiAccount,
    mdiEye,
    mdiEyeOff,
    mdiLock,
    mdiClose,
  }

  private formValue: { [key: string]: string | null } = {
    username: null,
    password: null,
  }

  private get isFormEmpty(): boolean {
    const { username, password } = this.formValue || {};

    if ((username === null
      || username === undefined
      || username.toString() === '')
      && (password === null
      || password === undefined
      || password.toString() === '')) {
      return true;
    }
    return false;
  }

  private onClearClick(): void {
    // eslint-disable-next-line
    (this.$refs.form as any).reset();
  }

  @Emit('loginstart')
  private loginstart() {
    return this.$vuetify;
  }

  private onSubmitForm() {
    this.loginstart();
    axios({
      url: 'http://yan.terni.ru/cp/api/settings',
      method: 'GET',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Credentials': true,
        'Content-Security-Policy': 'upgrade-insecure-requests',
      },
    })
      // eslint-disable-next-line
      .then((a: any) => console.log(123, a))
      // eslint-disable-next-line
      .catch((e: AxiosError) => console.log(45345, e.response))
      .finally(() => this.$emit('loginend'));
    console.log(this, 'SUBMIT', this.formValue);
  }
}
